
.header {
  position: relative;
  display: inline-block;
  width: 100%;
  margin-bottom: 2%;
}

.newGameButton {
  position: relative;
  display: inline-block;
  float: left;
  margin-left: 3em;

  font-family: 'PoppinsRegular';
  font-size: 1.2em;

  outline: none;
  padding: 0;
  border: none;
  background: none;
}

.buttonRow {
  position: relative;
  display: inline-block;
  text-align: center;

  width: 100%;
}

.button {
  position: relative;
  display: inline-block;

  font-family: 'PoppinsRegular';
  font-size: 1.2em;
  font-weight: bold;

  background-color: rgb(7, 76, 188);
  color: white;
  padding: 0.1em 0.5em 0.1em 0.5em;
  border-radius: 0.5em;
  margin-left: 1.5em;
  margin-right: 1.5em;

  outline: none;
  border: none;
}

.button:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05)
}

.button:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9)
}

.newGameButton:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05)
}

.newGameButton:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9)
}

.helpButton:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.helpButton:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}

.helpButton {
  position: relative;
  padding-right: 3em;
  width: 2em;
  height: 2em;
  float: right;
  margin: 0 auto;
}


